<!--  -->
<template>
  <div class="home-box">
    <p class="home-logo">
      <img src="../../assets/img08.png">
    </p>
    <p class="home-top-line">
      <img src="../../assets/img09.png">
    </p>
    <div class="home-right-top">
      <img src="../../assets/img07.png">
    </div>
    <div class="home-right-btm">
      <img src="../../assets/img07.png">
    </div>
    <div>
      <div class="lav-stat">
        
      </div>
      <div class="lav-echart">
        <lineEchart
          :option="lineOption"
          :width="'100%'"
          :height="400"
          :id="'levelE'"
        ></lineEchart>
      </div>
    </div>
  </div>
</template>

<script>
import lineEchart from '../VCharts/line'
export default {
  name: 'consumptionLevel',
  data () {
    return {
      
    }
  },
  methods:{
  	getLineData: function (){
      this.lineOption = {
        tooltip: {
          trigger: 'axis',
          // formatter: '{a} <br/>{b} : {c}'
        },
        legend: {
          left: 'center',
          top:20,
          data: ['', '','','',''],
          textStyle:{
            color:'#fff'
          }
        },
        xAxis: {
          show:true,
          type: 'category',
          splitLine: {show: false},
          data: ['', '', '', '', '', '', '', ''],
          nameTextStyle:{
            color:'#fff'
          },

          axisLine:{
            lineStyle:{
              color:"#fff"
            }
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '5%',
          containLabel: true
        },
        yAxis: {
          type: 'log',
          splitLine:{
            lineStyle:{
              color:"#0a306e"
            }
          },
          axisLine:{
            lineStyle:{
              color:"#fff"
            }
          }
        },
        series: [
        {
          name: '',
          type: 'line',
          data: [560, 870, 1320, 2740, 2810, 2470, 1690, 2052],
          symbolSize:10,
          lineStyle:{
            width:2
          }
        },
        {
          name: '',
          type: 'line',
          data: [1130, 1420,1661, 1208, 3016, 2532, 2264, 1188],
          symbolSize:10,
          lineStyle:{
            width:2
          }
        },
        {
          name: '',
          type: 'line',
          data: [2030, 2420,2661, 2208, 3016, 2532, 2264, 3188],
          symbolSize:10,
          lineStyle:{
            width:2
          }
        },
        {
          name: '',
          type: 'line',
          data: [2430, 3420,2661, 2208, 3016, 4532, 5264, 4188],
          symbolSize:10,
          lineStyle:{
            width:2
          }
        },
        {
          name: '',
          type: 'line',
          data: [312, 400, 800, 400, 406, 712, 524, 1030],
          symbolSize:10,
          lineStyle:{
            width:2
          }
        }
        ]
      };
    }
  },
  computed:{

  },
  created: function () {
  	this.getLineData();
  },
  components:{
  	lineEchart
  }
}
</script>

<style scoped>
	.life-box{
    position: relative;
    z-index: 100;
  }
  .lav-stat{
    margin: 40px 100px;
    padding: 0 20px;
    width: calc(100% - 240px);
    text-align: center;
    height: 42px;
    line-height: 42px;
    border-radius: 42px;
    background-color: rgba(0,0,0,0.3);
  }
  .lav-stat span{
    margin-right: 10px;
  }
  .lav-echart{
    margin: 40px 100px;
    width: calc(100% - 240px);
    background-color: rgba(0,0,0,0.1);
    height: 400px;
    padding: 0 20px;
  }
</style>


