<!--  -->
<template>
  <div class="home-box">
    <p class="home-logo">
      <img src="../../assets/img08.png">
    </p>
    <p class="home-top-line">
      <img src="../../assets/img09.png">
    </p>
    <div class="home-right-top">
      <img src="../../assets/img07.png">
    </div>
    <div class="home-right-btm">
      <img src="../../assets/img07.png">
    </div>
    <div class="life-box">
      <div class="lav-stat">
        
        <span>1</span>
        
        <span>125</span>
        /
        <span>125/3000</span>
      </div>
      <div class="lav-echart">
        <lineEchart
          :option="lineOption"
          :width="'100%'"
          :height="400"
          :id="'lavLine'"
        ></lineEchart>
      </div>
      <div class="lav-stat">
        
      </div>
      <div class="lav-echart">
        <forceDynamic
          :option="forceOption"
          :width="'100%'"
          :height="400"
          :id="'lavforce'"
        ></forceDynamic>
      </div>
    </div>
  </div>
</template>

<script>
import lineEchart from '../VCharts/line'
import forceDynamic from '../VCharts/forceDynamic.vue'
export default {
  name: 'lifeLaw',
  data () {
    return {
      lineOption:{},
      forceOption:{}
    }
  },
  methods:{
  	getLineData: function (){
      this.lineOption = {
        tooltip: {
          trigger: 'axis',
          // formatter: '{a} <br/>{b} : {c}'
        },
        legend: {
          left: 'center',
          top:20,
          data: ['', '',''],
          textStyle:{
            color:'#fff'
          }
        },
        xAxis: {
          show:true,
          type: 'category',
          splitLine: {show: false},
          data: ['', '', '', '', '', '', '', ''],
          nameTextStyle:{
            color:'#fff'
          },

          axisLine:{
            lineStyle:{
              color:"#fff"
            }
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '5%',
          containLabel: true
        },
        yAxis: {
          type: 'log',
          splitLine:{
            lineStyle:{
              color:"#0a306e"
            }
          },
          axisLine:{
            lineStyle:{
              color:"#fff"
            }
          }
        },
        series: [
        {
          name: '',
          type: 'line',
          data: [56, 87, 132, 274, 181, 247, 69, 52],
          symbolSize:10,
          lineStyle:{
            width:2
          }
        },
        {
          name: '',
          type: 'line',
          data: [230, 420, 661, 1208, 316, 532, 264, 188],
          symbolSize:10,
          lineStyle:{
            width:2
          }
        },
        {
          name: '',
          type: 'line',
          data: [1, 4, 8, 4, 6, 12, 24, 30],
          symbolSize:10,
          lineStyle:{
            width:2
          }
        }
        ]
      };
    }
  },
  computed:{
    
  },
  created: function () {
  	this.getLineData()
  },
  components:{
  	lineEchart,
    forceDynamic,
  }
}
</script>

<style scoped>
  .life-box{
    position: relative;
    z-index: 100;
  }
	.lav-stat{
    margin: 40px 100px;
    padding: 0 20px;
    width: calc(100% - 240px);
    text-align: center;
    height: 42px;
    line-height: 42px;
    border-radius: 42px;
    background-color: rgba(0,0,0,0.3);
  }
  .lav-stat span{
    margin-right: 10px;
  }
  .lav-echart{
    margin: 40px 100px;
    width: calc(100% - 240px);
    background-color: rgba(0,0,0,0.1);
    height: 400px;
    padding: 0 20px;
  }
</style>


