<template>
  <div class="line">
  <!-- width -->
    <div :id="id" :style="{width: width, height: height+'px'}"></div>
  </div>
</template>

<script>
import Vue from 'vue'
import echarts from 'echarts'
Vue.prototype.$echarts = echarts 
export default {
  name: 'lineEchart',
  props:["option","width","height","id"],
  data:function () {
    return {
    	
    }
  },
  created:function (){
  	
  },
  updated:function (){

  },
  mounted(){
    this.drawLine();
  },
  methods: {
    drawLine(){
        // domecharts
        let domId = this.id?document.getElementById(this.id):null;
        let myChart = this.$echarts.init(domId);
        let option = {
            title: this.option.title || {
             
            },
            tooltip: this.option.tooltip || {
              trigger: 'item',
              formatter: '{a} <br/>{b} : {c}'
            },
            legend: this.option.legend || {},
            xAxis: this.option.xAxis || {
              show:true,
              type: 'category',
              name: 'x',
              splitLine: {show: false},
              data: [],
              nameTextStyle:{
                color:'#fff'
              },
              
              axisLine:{
                lineStyle:{
                  color:"#fff"
                }
              }
            },
            grid: this.option.grid || {
              left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
            },
            yAxis: this.option.yAxis || {
              type: 'log',
              name: 'y',
              splitLine:{
                lineStyle:{
                  color:"#0a306e"
                }
              },
              axisLine:{
                lineStyle:{
                  color:"#fff"
                }
              }
            },
            series: this.option.series || []
          };
        // 
        myChart.setOption(option);
    }
  }
}
</script>

<style scoped>
	h1{
		color: #fff;
	}
</style>


