<template>
  <div class="line">
    <div :id="id" :style="{width: width, height: height+'px'}"></div>
  </div>
</template>

<script>
import Vue from 'vue'
import echarts from 'echarts'
Vue.prototype.$echarts = echarts 
export default {
  name: 'funnelEcharts',

  props:["option","width","height","id"],
  data:function () {
    return {
    	
    }
  },
  created:function (){
  	
  },
  updated:function (){

  },
  mounted(){
    this.drawLine();
  },
  methods: {
    drawLine(){
      console.log(this.id)
        // domecharts
        let domId = this.id ? document.getElementById(this.id):null;
        let myChart = this.$echarts.init(domId);
        let option = {
            title: this.option.title || {
                // text: '',
            },
            tooltip:this.option.tooltip || {
                trigger: 'item',
                formatter: "{a} <br/>{b} : {c}%"
            },
            legend:this.option.legend || {
                show:false
            },
            calculable: true,
            series: this.option.series || []
        };
        // 
        myChart.setOption(option);
    }
  }
}
</script>

<style scoped>
	h1{
		color: #fff;
	}
</style>


