import Vue from 'vue'
import Router from 'vue-router'



import HelloWorld from '@/components/HelloWorld'
import Home from '../components/home/home.vue'
import LeftSide from '../components/leftSide/leftSide.vue'
import LineLaw from '../components/lifeLaw/lifeLaw.vue'
import academicInfo from '../components/academicInfo/academicInfo.vue'
import socialConnections from '../components/socialConnections/socialConnections.vue'
import consumptionLevel from '../components/consumptionLevel/consumptionLevel.vue'
import mentalHealth from '../components/mentalHealth/mentalHealth.vue'
import growthGuidance from '../components/growthGuidance/growthGuidance.vue'
import financingInformation from '../components/financingInformation/financingInformation.vue'
import workAttendance from '../components/workAttendance/workAttendance.vue'
import socialActivities from '../components/socialActivities/socialActivities.vue'
import myAlbum from '../components/myAlbum/myAlbum.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/leftSide',
      name: 'leftSide',
      component: LeftSide
    },
    {
      path: '/LineLaw',
      name: 'LineLaw',
      component: LineLaw
    },
    {
      path: '/academicInfo',
      name: 'academicInfo',
      component: academicInfo
    },
    {
      path: '/socialConnections',
      name: 'socialConnections',
      component: socialConnections
    },
    {
      path: '/consumptionLevel',
      name: 'consumptionLevel',
      component: consumptionLevel
    },
    {
      path: '/mentalHealth',
      name: 'mentalHealth',
      component: mentalHealth
    },
    {
      path: '/growthGuidance',
      name: 'growthGuidance',
      component: growthGuidance
    },
    {
      path: '/financingInformation',
      name: 'financingInformation',
      component: financingInformation
    },
    {
      path: '/workAttendance',
      name: 'workAttendance',
      component: workAttendance
    },
    {
      path: '/socialActivities',
      name: 'socialActivities',
      component: socialActivities
    },
    {
      path: '/myAlbum',
      name: 'myAlbum',
      component: myAlbum
    }
  ]
})



