import waterfall from 'vue-waterfall/lib/waterfall'
import waterfallSlot from 'vue-waterfall/lib/waterfall-slot'
import waterfalls from '../VCharts/waterfall'
export default {
  name: 'myAlbum',
  data () {
    return {
      items:[
        {width:200,height:300,id:"001",srcs:"001"},
        {width:200,height:400,id:"002",srcs:"002"},
        {width:200,height:100,id:"003",srcs:"001"},
        {width:200,height:200,id:"004",srcs:"002"},
        {width:200,height:400,id:"005",srcs:"001"},
        {width:200,height:300,id:"006",srcs:"002"},
        {width:200,height:400,id:"007",srcs:"002"},
        {width:200,height:400,id:"008",srcs:"002"},
        {width:200,height:400,id:"009",srcs:"002"},
        {width:200,height:200,id:"010",srcs:"001"},
        {width:200,height:400,id:"011",srcs:"002"},
        {width:200,height:400,id:"012",srcs:"002"},
        {width:200,height:400,id:"013",srcs:"002"},
        {width:200,height:400,id:"014",srcs:"001"},
        {width:200,height:400,id:"015",srcs:"002"},
      ],
    }
  },
  methods:{
  	
  },
  computed:{

  },
  created: function () {
  	
  },
  components:{
  	waterfall,
    waterfallSlot,
    waterfalls
  }
}
</script>

<style scoped>
  .my-pic-list{
    height: 1700px;
  }
  .pic-item{
    height: 80%;
  }
</style>


