<template>
  <div id="app">
    <div class="sidebar">
        <div class="sidebar-line-top">
            <img src="../assets/img01.png">
        </div>
        <div class="sidebar-line-right">&nbsp;</div>
        <div class="sidebar-line-foot">
            <img src="../assets/img01.png">
        </div>
        <div class="lf people">
            <p><img src="../assets/dome2.jpg"></p>
            <b>{{personal.name}}</b>
            <em>cdu</em>
        </div>
        <div class="lf maxbox menu">
            <ul>
              <!-- <li><span class="cur"><b><router-link to="/"></router-link></b><i>&nbsp;</i><em>&nbsp;</em></span></li>
              <li><span><b><router-link to="leftSide"></router-link></b><i>&nbsp;</i><em>&nbsp;</em></span></li> -->
              <li v-for="item in aside"  v-on:click="asideEvent(item)"><span v-bind:class="{'cur':item.name==asideName}"><b><router-link :to="item.router">{{item.name}}</router-link></b><i>&nbsp;</i><em>&nbsp;</em></span></li>
          </ul>
      </div>
  </div>
  <div class="app-main">
    <div class="app-header">
        <p>
            <span>CDU</span> 
        </p>
    </div>
    <div class="app-container">
        <router-view></router-view>
    </div>
</div>


</div>
</template>

<script>
    export default {
      name: 'App',
      data:function (){
        return {
            personal:{
                name:'vvmily'
            },
            aside:[],
            asideName:""
        }
      },
      created:function(){
        this.$router.push({ path: '/' });//
        this.getAsideData();
      },
      methods:{
        getAsideData: function (){
            let data = [
                {name:"",router:"/",id:"001"},
                {name:"",router:"LineLaw",id:"001"},
                {name:"",router:"academicInfo",id:"001"},
                {name:"",router:"socialConnections",id:"001"},
                {name:"",router:"consumptionLevel",id:"001"},
                {name:"",router:"mentalHealth",id:"001"},
                {name:"",router:"growthGuidance",id:"001"},
                {name:"",router:"financingInformation",id:"001"},
                {name:"",router:"workAttendance",id:"001"},
                {name:"",router:"socialActivities",id:"001"},
                {name:"",router:"myAlbum",id:"001"},
            ];
            this.aside = data;
            this.asideName = data[0].name;
        },
        asideEvent: function (item){
            this.asideName = item.name;
        }
      }
  }
</script>

<style>
    

</style>



