<!--  -->
<template>
  <div class="home-box">
    <p class="home-logo">
      <img src="../../assets/img08.png">
    </p>
    <p class="home-top-line">
      <img src="../../assets/img09.png">
    </p>
    <div class="home-right-top">
      <img src="../../assets/img07.png">
    </div>
    <div class="home-right-btm">
      <img src="../../assets/img07.png">
    </div>
    <div>
      <div class="home-echart">
        <funnelEchart 
        :option="funnelOption"
        :width="'400px'"
        :height="300"
        :id="'social01'"
        ></funnelEchart>
      </div>
      <div class="home-echart">
        <funnelEchart 
        :option="funnelOption"
        :width="'400px'"
        :height="300"
        :id="'social02'"
        ></funnelEchart>
      </div>
    </div>
  </div>
</template>

<script>
import funnelEchart from '../VCharts/funnelPlot'
export default {
  name: 'socialConnections',
  data () {
    return {
      
    }
  },
  methods:{
  	getFunnelData :function (){
      this.funnelOption = {
            series: [
                {
                    name:'',
                    type:'funnel',
                    left: '10%',
                    top: 60,
                    bottom: 60,
                    width: '80%',
                    min: 0,
                    // max: 100,
                    minSize: '0%',
                    maxSize: '100%',
                    sort: 'descending',
                    gap: 2,
                    label: {
                        normal: {
                            show: true,
                            position: 'inside'
                        },
                        emphasis: {
                            textStyle: {
                                fontSize: 14
                            }
                        }
                    },
                    labelLine: {
                        normal: {
                            length: 10,
                            lineStyle: {
                                width: 1,
                                type: 'solid'
                            }
                        }
                    },
                    itemStyle: {
                        normal: {
                            borderColor: '#fff',
                            borderWidth: 0
                        }
                    },
                    data: [
                        {value: 60, name: ''},
                        {value: 40, name: ''},
                        {value: 20, name: ''},
                    ]
                }
            ]
        };
    },
  },
  computed:{

  },
  created: function () {
  	this.getFunnelData()
  },
  components:{
  	funnelEchart
  }
}
</script>

<style scoped>
	
</style>


