<template>
  <div class="forceDynamic">
    <div :id="id" :style="{width: width, height: height+'px'}"></div>
  </div>
</template>

<script>
import Vue from 'vue'
import echarts from 'echarts'
Vue.prototype.$echarts = echarts 
export default {
  name: 'forceDynamic',
  props:["option","width","height","id"],
  data:function () {
    return {
    	
    }
  },
  created:function (){
  },
  updated:function (){

  },
  mounted(){
    this.drawLine();
  },
  methods: {
    drawLine : function () {
        // domecharts
        let domId = this.id?document.getElementById(this.id):null;
        let myChart = this.$echarts.init(domId);
        let city = ['','','','','','','','','','','','']
        var data = [{
          fixed: true,
          x: myChart.getWidth() / 2,
          y: myChart.getHeight() / 2,
          symbolSize: 30,
          id: '-1',
          name:'VVMILY'
        }];
        var edges = [];
        let option = {
          tooltip : {
            trigger: 'item',
            formatter: "{b}"
          },
          series: [{
            type: 'graph',
            layout: 'force',
            animation: false,
            data: data,
            force: {
              repulsion: 100,
              edgeLength: 15
            },
            edges: edges,
            emphasis:{
                label:{
                    show:false
                },

            },
            itemStyle:{
              normal:{
                color:'#fff'
              },
              emphasis:{
                color:'#16f0fa',
                borderWidth:4,
                borderColor:'rgba(20,194,213,0.8)'
              }
            }
          }]
        };
        let times =  setInterval(function () {
          data.push({
            id: data.length,
            name:city[Math.floor(Math.random()*10)],
            symbolSize: (Math.floor(Math.random()*10)+10),
          });
          var source = Math.round((data.length - 1) * Math.random());
          var target = Math.round((data.length - 1) * Math.random());
          if (source !== target) {
            edges.push({
              source: source,
              target: target
            });
          }
          myChart.setOption({
            series: [{
              roam: true,
              data: data,
              edges: edges
            }]
          });
          if(data.length>300){
            clearInterval(times)
          }
        }, 1500);
        // 
        myChart.setOption(option);
        myChart.on('click',function(event){
          // console.log(event)
        });

    }
  }
}
</script>

<style scoped>
	h1{
		color: #fff;
	}
</style>


